/** TRAIL INFO **/

#trail_info {
  background-color: rgb(43, 43, 43);
}
#trail_info h1 {
  color: white;
  margin: auto;
  padding: 5px;
}

/** MEMORY INFO **/

#memory_info {
  overflow: auto;
  height: 70vh;
  margin: 20px 0 0;
  display: grid;
  grid-template-areas: 
    "date contributor"
    "document document";
  grid-template-rows: -webkit-min-content auto;
  grid-template-rows: min-content auto;
}
#date {
  grid-area: date;
  display: flex;
  justify-content: flex-start;
}
#contributor {
  grid-area: contributor;
  display: flex;
  justify-content: flex-end;
}
#date p, #contributor p {
  margin: 0;
}
.document > *, .sub_docs > * {
  width: 100%;
  max-height: 80vh;
}
.document {
  grid-area: document;
  padding: 0 40px;
}
.document * {
  margin-top: 20px;
}
.document p {
  text-indent: 10%;
  white-space: pre-line;
}

.document p .highlightedText {
  font-size: 1.3em;
  font-weight: bold;
  color: rgb(57,234, 234);
}

.sub_docs {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin: auto;
}
audio {
  outline: none;
}

