.docbuttons {
    /* margin: 0 auto; */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: grid;
    grid-template-areas: 
    ". nextOther ."
    "prevCurrent centerButton nextCurrent"
    ". prevOther .";
}
.arrowbutton_img {
    width: auto;
    height: 25px;
    -webkit-filter: grayscale(100%) brightness(0.2);
            filter: grayscale(100%) brightness(0.2);
}
#trail_img {
    -webkit-filter: grayscale(100%) brightness(1);
            filter: grayscale(100%) brightness(1);
}


.button-current.previous {
    grid-area: prevCurrent;
    display: flex;
    align-items: center;
}
.button-current.next {
    grid-area: nextCurrent;
    display: flex;
    align-items: center;
}
.button-current.next img {
    transform: rotate(180deg);
}
.button.previous {
    grid-area: prevOther;
}
.button.previous img {
    transform: rotate(-90deg) translateX(5px);
}
.button.next {
    grid-area: nextOther;
    display: flex;
    justify-content: center;
}
.button.next img {
    transform: rotate(90deg) translateX(5px);
}
.arrowbutton_img:hover {
    -webkit-filter: hue-rotate(104deg);
            filter: hue-rotate(104deg);
    cursor: pointer;
}

.centerButton {
    grid-area: centerButton;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(43, 43, 43);
    border-radius: 50%;
    width: 40px;
    height: 40px;
}
